.WishCard {
    padding: 24px;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto 50px;
    grid-column-gap: 5px;
    grid-row-gap: 5px;
    min-height: 200px;
    margin-bottom: 10px;
    grid-template-areas: 
      "Wishcard-Content Wishcard-Content"
      ". Wishcard-Toolbar";
}

.WishCard-Content {
    grid-area: Wishcard-Content;
}

.WishCard-Toolbar {
    -ms-flex-item-align: end;
        align-self: flex-end;
    justify-self: flex-end;
    grid-area: Wishcard-Toolbar;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.WishCard-Toolbar > * {
    margin-left: 5px;
}


