.DrawerGrid {
    display: grid;
    min-width: 230px;
    grid-template-areas: 
    "Header"
    "Content";
}

.DrawerGrid a {
    text-decoration: none;
}

.DrawerGrid-Header {
    grid-area: Header;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #F44336;
    padding-left: 16px;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 125px;
}

.DrawerGrid-Content {
    grid-area: Content;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}


