.MainHeader {
    grid-area: header;
    background-color: #F44336;
    position: relative;
    color: white;
    max-height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    padding: 16px;
    -webkit-transform: translateY(-100px);
        -ms-transform: translateY(-100px);
            transform: translateY(-100px);
    -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    transition: -webkit-transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    -o-transition: transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    transition: transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    transition: transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1), -webkit-transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    -webkit-box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
            box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.MainHeader.slideIn { 
    -webkit-transform: translateY(0px); 
        -ms-transform: translateY(0px); 
            transform: translateY(0px);
}

.SantaHat {
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background-color: #FF5252;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
}

.SantaHat-Icon {
    height: 50px;
    width: 50px;
    color: white;
}

.DrawerIcon {
    color: white;
    text-decoration: none;
    height: 35px!important;
    width: 35px!important;
}

.ButtonLink {
    height: 100%;
    font-size: 1rem!important;
    color: white!important;
    font-weight: 400!important;
}


@media (max-width: 700px) { 
    .HeaderTop {
        position: relative;
    }
}


