.User {
    margin-top: 16px;
}

.User-Info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-filter: drop-shadow(0 16px 24px rgba(0, 0, 0, 0.12))
    drop-shadow(0 6px 30px rgba(0, 0, 0, 0.14))
    drop-shadow(0 8px 10px rgba(0, 0, 0, 0.12))
    drop-shadow(0 -3px 4px rgba(0, 0, 0, 0.1));
            filter: drop-shadow(0 16px 24px rgba(0, 0, 0, 0.12))
    drop-shadow(0 6px 30px rgba(0, 0, 0, 0.14))
    drop-shadow(0 8px 10px rgba(0, 0, 0, 0.12))
    drop-shadow(0 -3px 4px rgba(0, 0, 0, 0.1));
    z-index: 20;
    position: relative;
}

.User-Content { 
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 200px;
    margin-bottom: 16px;
    text-align: center;
}

.User-Content > h1 {
    margin-bottom: 0.2rem;
}

.User-image {
    grid-area: Image;
    -webkit-transition: -webkit-filter ease 0.3s;
    transition: -webkit-filter ease 0.3s;
    -o-transition: filter ease 0.3s;
    transition: filter ease 0.3s;
    transition: filter ease 0.3s, -webkit-filter ease 0.3s;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    max-height: 400px;
    max-width: 400px;
    border-radius: 50%;
    -webkit-transform: scale(0.2);
        -ms-transform: scale(0.2);
            transform: scale(0.2);
    opacity: 0;
    position: relative;
    z-index: 20;
    -webkit-transition: all 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
    -o-transition: all 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
    transition: all 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
}

@media (max-width: 500px) {
    .User-image {
        max-height: 300px;
        max-width: 300px;
    }
    .User-image > img {
        max-height: 300px!important;
    }
    .User-Wishes {
        bottom: 150px!important;
    }
    .User-Content {
        padding-top: 150px;
    }
  }


.User-image.PopIn {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.User-image > img {
    height: auto;
    width: auto;
    max-height: 500px;
}

.User-Nickname {
    margin: 0;
    margin-top: 32px;
}

.User-Wishes {
    background-color: white;
    position: relative;
    bottom: 200px;
    z-index: 10;
    padding: 16px;
    -webkit-box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 
    0px 1px 10px 0px rgba(0, 0, 0, 0.12);
            box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 
    0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.UserWishCard {
    border-top: 1px solid #BDBDBD;
}


